import React, {Component} from 'react';
import {Container, Row, Col, Card, Button} from 'react-bootstrap';
import styled from 'styled-components';
import jumboHome from './assets/jumboHome.jpg';
import backgroundHome from './assets/test.jpg';
import DailyPicImage from "./assets/homeDailyPics.jpg";
import MarsImage from "./assets/homeMars.jpg";
import NaturalEventsImage from "./assets/homeNaturalEvents.jpg";
import {Link} from 'react-router-dom';
import TitleBackground from './assets/homeTitleBackground.png';

import Particles from "react-particles-js";

const Styles = styled.div`
  .homeImage{
      position:relative;
      margin:auto;
      display: block;
      height: 250px;
      width: auto;
  }
 .imgContainer {
      position: relative;
      margin: auto;
      overflow: hidden;
 }
 .titleContainer {
    position:relative;
    margin:auto;
   }
    .buttonText{
       position: relative;
       margin:auto;
       color:#FFFFFF;
       font-size:25px;
       text-decoration: none;
       &:hover{
          color:#d3d3d3;
       }
    }
    .titleText{
       position: relative;
       margin:auto;
       color:#FFFFFF;
       font-size:125px;
       font-weight:650;
    }
    .titleSubtext{
       position: relative;
       margin:auto;
       color:#FFFFFF;
       font-size:17px;
       font-weight:100;
       background-color:rgba(0,0,0,0.7);
       padding:10px;
       padding-top:4px;
       padding-bottom:4px;
       background-size:cover;
       border-radius: 7px;
    }
    .titleParatext{
       position: relative;
       margin:auto;
       color:#FFFFFF;
       font-size:25px;
       font-weight:350;
       text-align: center;
    }
    .backgroundContainer{
      fontFamily: sans-serif;
      textAlign: center;
      width:100%;
      height: 100%;
      background: #222;
      display: flex;
      justifyContent: center;
      alignItems: center;
      position: absolute;
      z-index: 0;
    }
    a {
      &:hover {
         text-decoration: none;
      }
    }
`;
const StyledCard = styled(Card)`
  position:relative;
  margin:auto;
  color:#FFFFFF;
`;
const StyledCardText = styled(Card.Text)`
  color:#FFFFFF;
`;
const StyledCardTitle = styled(Card.Title)`
   color:#FFFFFF;
`;
const StyledCardHeader = styled(Card.Header)`
   color:#FFFFFF;
`;
const StyledCol = styled(Col)`
  position:relative;
  margin:auto;
`;
const StyledRow = styled(Row)`
  position:relative;
  padding-top:15px;
  padding-bottom:15px;
`;
const StyledContainer = styled(Container)`
  position:relative;
  margin:auto;
  padding-left:125px;
  padding-right:125px;
`;
const StyledButton = styled(Button)`
   padding:2px;
   border-width:4px;
`;

class Home extends Component{
  constructor(props){
    super(props);
    this.state = {
      width:0,
      height:0,
      cardInfo:
         [
            {header:"Educate yourself daily with hand-picked visuals chosen by Astronauts", title:"Astronomers' Picture of the Day!", text:"Everyday our website is updated with a new eye-opening image along with a short paragaph describing the wonders of life which spans from renders of galaxies to a video of the Earth during the solar eclipse."},
            {header:"Wonder at what life on Mars thanks to our very own rover, Curiosity!", title:"Life at Mars", text:"Ever wondered what life on Mars would feel like? Head over to the Mars section to find out what the living conditions of Mars are currently like through weather reports and stunning photos of the Red Planet."},
            {header:"Have a top-down look at the Natural Events as they occur!", title:"Lansat Imagery of Earth's Natural Events", text:"Using NASA's Lanstat Imagery API and it's records of Natural Disasters, we were able to create an ever-updating database of Natural Events, giving you the power to take a look."},
         ],
    };
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  async componentDidMount(){
      this.updateWindowDimensions();
      window.addEventListener('resize', this.updateWindowDimensions);
      let jumboHeader = "";
      let jumboText = "Explore the Universe";
      this.props.onChangePage(backgroundHome, jumboHome, jumboHeader, jumboText);
  }

  componentWillUnmount(){
    window.removeEventListener('resize', this.updateWindowDimensions);
  }
  updateWindowDimensions(){
    this.setState({width:window.innerWidth, height:window.innerHeight});
  }

  render(){
    return(
      <Styles>
         <div className="titleContainer">
            <Particles className="backgroundContainer" />
            <StyledRow style={{paddingTop:"0px", paddingBottom:"0px", marginLeft:"50px", marginRight:"50px"}}>
               <h1 className="titleText">Enter The Hubble</h1>
            </StyledRow>
            <StyledRow style={{paddingTop:"0px", marginLeft:"50px", marginRight:"50px"}}>
               <h1 className="titleSubtext">Mesmerizing Images | The Red Planet | Natural Disasters</h1>
            </StyledRow>
         </div>
         <StyledContainer style={{paddingLeft:"0px",paddingRight:"0px",}} fluid>
            <Particles className="backgroundContainer"/>
            <StyledRow style={{marginLeft:"175px",marginRight:"175px",}}>
            <h1 className="titleParatext">With help from our friends at NASA, you are given the opportunity to feast at some breath-taking visuals from outer space. From mesmerizing photos hand-picked by the professionals working at Nasa to sceneries of Mars taken by Curiosity, explore the website and allow it to blow your mind. </h1>
            </StyledRow>
            <StyledRow style={{paddingTop:"50px", marginLeft:"175px",marginRight:"175px"}}>
               <StyledCol lg={5}>
                  <div className="imgContainer">
                     <img className="homeImage" src={DailyPicImage} alt={"TEMPORARY"}/>
                  </div>
               </StyledCol>
               <StyledCol lg={7}>
                  <StyledCard style={{backgroundColor:"rgba(25,36,46,0.8)"}}>
                     <StyledCardHeader style={{backgroundColor:"#41688b"}}>{this.state.cardInfo[0].header}</StyledCardHeader>
                     <Card.Body>
                        <StyledCardTitle>{this.state.cardInfo[0].title} </StyledCardTitle>
                        <StyledCardText> {this.state.cardInfo[0].text} </StyledCardText>
                        <StyledRow style={{float:"right"}} >
                           <StyledButton style={{backgroundColor:"#41688b", borderColor:"#395b7a"}} variant="success"> <Link className="navStyledButton" to="/hubble/dailypic"> <h1 textColor="#395b7a" className="buttonText">{"Check out Today's Picture"}</h1> </Link> </StyledButton>
                        </StyledRow>
                     </Card.Body>
                  </StyledCard>
               </StyledCol>
            </StyledRow>
            <StyledRow style={{marginLeft:"175px",marginRight:"175px",}}>
               <StyledCol lg={7}>
                  <StyledCard style={{backgroundColor:"rgba(36,0,7,0.8)"}}>
                     <StyledCardHeader style={{backgroundColor:"#a9343a"}}>{this.state.cardInfo[1].header}</StyledCardHeader>
                     <Card.Body>
                        <StyledCardTitle> {this.state.cardInfo[1].title} </StyledCardTitle>
                        <StyledCardText> {this.state.cardInfo[1].text} </StyledCardText>
                        <StyledRow style={{float:"right"}} >
                           <StyledButton variant="success" style={{backgroundColor:"#a9343a", borderColor:"#962e33"}}> <Link className="navStyledButton" to="/hubble/mars"> <h1 textColor="#760015" className="buttonText">{"Visit Mars"}</h1> </Link> </StyledButton>
                        </StyledRow>
                     </Card.Body>
                  </StyledCard>
               </StyledCol>
               <StyledCol lg={5}>
                  <div className="imgContainer">
                     <img className="homeImage" src={MarsImage} alt={"TEMPORARY"}/>
                  </div>
               </StyledCol>
            </StyledRow>
            <StyledRow style={{marginLeft:"175px",marginRight:"175px",}}>
               <StyledCol lg={5} style={{paddingBottom:"70px"}}>
                  <div className="imgContainer">
                     <img className="homeImage" src={NaturalEventsImage} alt={"TEMPORARY"}/>
                  </div>
               </StyledCol>
               <StyledCol lg={7} style={{paddingBottom:"70px"}}>
                  <StyledCard style={{backgroundColor:"rgba(35,36,4,0.8)"}}>
                     <StyledCardHeader style={{backgroundColor:"#a3a812"}}>{this.state.cardInfo[2].header}</StyledCardHeader>
                     <Card.Body>
                        <StyledCardTitle> {this.state.cardInfo[2].title} </StyledCardTitle>
                        <StyledCardText> {this.state.cardInfo[2].text} </StyledCardText>
                        <StyledRow style={{float:"right"}} >
                           <StyledButton style={{backgroundColor:"#a3a812", borderColor:"#8d9110"}} variant="success"> <Link className="navStyledButton" to="/hubble/naturalevents"> <h1 textColor="#767a0d" className="buttonText">{"Explore Natural Events"}</h1> </Link> </StyledButton>
                        </StyledRow>
                     </Card.Body>
                  </StyledCard>
               </StyledCol>
            </StyledRow>
         </StyledContainer>
      </Styles>
    );
  }
}
export default Home;
